import React, { Component } from 'react';

import Button from '@material-ui/core/Button';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import SportsSoccerIcon from '@material-ui/icons/SportsSoccer';
import Box from '@material-ui/core/Box';
import Divider from '@material-ui/core/Divider';

import Checkbox from './Checkbox'
import Teams from './Teams'
import * as Util from './Util'

const PLAYERS = {
  'Aady':       { name: 'Aady', weight: 4, checked: false },
  'Adarsh':     { name: 'Adarsh', weight: 3, checked: false },
  'Adi':        { name: 'Adi', weight: 3, checked: false },
  'Akshay':     { name: 'Akshay', weight: 5, checked: false },
  'Amit':       { name: 'Amit', weight: 1, checked: false },
  'Amog':       { name: 'Amog', weight: 1, checked: false },
  'Anas':       { name: 'Anas', weight: 4, checked: false },
  'Atif':       { name: 'Atif', weight: 3, checked: false },
  'Avi':        { name: 'Avi', weight: 3, checked: false },
  'Bharath':    { name: 'Bharath', weight: 4, checked: false }, 
  'Deep':       { name: 'Deep', weight: 4, checked: false }, 
  'Gourav':     { name: 'Gourav', weight: 5, checked: false },
  'Hardik':     { name: 'Hardik', weight: 1, checked: false },
  'Maddy':      { name: 'Maddy',  weight: 4, checked: false },
  'Mohit':      { name: 'Mohit',  weight: 4, checked: false },
  'Nilesh':     { name: 'Nilesh', weight: 4, checked: false },
  'Pavan':      { name: 'Pavan', weight: 1, checked: false },
  'Prabhanshu': { name: 'Prabhanshu', weight: 3, checked: false },
  'Piyush':     { name: 'Piyush', weight: 1, checked: false },
  'Rahul':      { name: 'Rahul' ,  weight: 2, checked: false }, 
  'Rohan':      { name: 'Rohan', weight: 3, checked: false },
  'Rohith':     { name: 'Rohith', weight: 5, checked: false },
  'Sheily':     { name: 'Sheily', weight: 2, checked: false },
  'Shriram':    { name: 'Shriram', weight: 2, checked: false },
  'Shreyas':    { name: 'Shreyas', weight: 3, checked: false },
  'Shub':       { name: 'Shub', weight: 4, checked: false },
  'Sky':        { name: 'Sky', weight: 3, checked: false },
  'Theo':       { name: 'Theo', weight: 4, checked: false },
  'Vineet':     { name: 'Vineet', weight: 3, checked: false },
  'Vivi':       { name: 'Vivi', weight: 3, checked: false },
  'Zaid':       { name: 'Zaid', weight: 4, checked: false }
}

class App extends Component {
  state = {
    checkboxes: PLAYERS 
  }

  selectAll        = () => this.selectAllCheckboxes(true);
  deSelectAll      = () => this.selectAllCheckboxes(false);
  createCheckboxes = () => Object.values(PLAYERS).map(player => 
    this.createCheckbox(player)
  );

  selectAllCheckboxes = isSelected => {
    this.setState(function(prevState) {
      Object.values(prevState.checkboxes).forEach((player) => player.checked = isSelected)
      return {
        checkboxes: prevState.checkboxes
      }
    })
  };

  handleCheckboxChange = changeEvent => {
    const playerName   = changeEvent.target.name;

    this.setState(function(prevState) {
      var playerObj = prevState.checkboxes[playerName];
      playerObj.checked = !playerObj.checked;

      return { 
        checkboxes: prevState.checkboxes
      }
    })
  };

  createCheckbox = player => {
    return(
      <Checkbox
        label={player.name}
        isSelected={player.checked}
        onCheckboxChange={this.handleCheckboxChange}
        key={player.name}
      />
    )
  };

  render() {
    return (
      <div>
        <AppBar position="static">
          <Toolbar>
            <Typography variant="h6"> 
              Burnaby FC <SportsSoccerIcon/>
            </Typography>
          </Toolbar>
        </AppBar>

        <Box display="flex" justifyContent="center" alignItems="center" mb={2} mt={2}>
          { Util.hasMinPlayersSelected(this.state) ? <Teams totalPlayers={Util.selectedPlayers(this.state)} /> : null }
        </Box>

        <Divider light/>
        <Typography component="div" align="center">
          <Box fontSize="h3.fontSize" fontFamily="Monospace" fontWeight="fontWeightBold" m={1}>
            Players
          </Box>
        </Typography>
        <Divider light/>

        <Box display="flex" justifyContent="center" borderRight={1} borderLeft={1} mt={4} mb={5} style={{ maxHeight: 280, overflow: 'auto' }}>
          <div>
            {this.createCheckboxes()}
          </div>
        </Box>

        <div align="center">
          <Button variant="contained" size="small" color="primary" onClick={this.selectAll}> Select All </Button>&nbsp;&nbsp;
          <Button variant="contained" size="small" color="secondary" onClick={this.deSelectAll}> Deselect All </Button>
        </div>
      </div>
    );
  }
}

export default App;
